import { Component, OnInit, Input, Output, EventEmitter, trigger, state, transition, style, animate } from '@angular/core';
import { FormBuilder, Validators, FormControl } from '@angular/forms';
import { CustomValidators } from '../custom-validators'

@Component({
  selector: 'app-form1',
  templateUrl: './form1.component.html',
  styleUrls: ['./form1.component.css'],
  animations: [
    trigger('slide', [
      state('out', style({height: '*'})),
      transition('* => void', [
        style({height: '*'}),
        animate(250, style({height: 0}))
      ])
    ])
  ]
})
export class Form1Component implements OnInit {
  @Input()
  formTitle: String;

  @Input() goStep2;

  @Output() stepValid = new EventEmitter();

  constructor(public fb: FormBuilder) {}
  public step1Form = this.fb.group({
    lstName: ['', [Validators.required, CustomValidators.noWhiteSpaces, Validators.pattern(CustomValidators.onlyLettersPattern)]],
    fstName: ['', Validators.pattern(CustomValidators.onlyLettersPattern)],
  });
  title = 'Simple form :';
  
  testFct = function(){
  }

  ngOnInit(){

  }

  validStep1(){
    this.goStep2(this.step1Form.value);
  }

  ngDoCheck(){
    this.stepValid.emit(this.step1Form.valid)
  }

}



